.container {
    position: sticky;
    top: 0;
    background-image: url(data:image/svg+xml;base64,);
    background-color: rgb(0, 30, 98);
}

.collapsableInputs {
    max-width: 80vw;
    margin: 0 auto;
    padding-left: 20px;
    padding-right: 20px;
    display: flex;
    justify-content: space-between;
    flex-grow: 1;
}

.inputsContainer {
    margin: 20px;
    margin-bottom: 10px;
}

.dropDownInput {
    position: absolute;
    padding: 20px;
    background-color: #fff;
    margin-top: -10px;
    width: 320px;
    display: flex;
    flex-direction: column;
    z-index: 1000;
}

.choose {
    display: grid;
    grid-gap: 20px;
    grid-template-columns: 1fr 1fr;
}

.chooseButton {    
    border: 2px solid grey;
    border-radius: 5px;
    padding: 5px 16px;
    min-height: 48px;
    font-size: 14px;
    outline: none;
}

.chooseButton:focus {
    border-color: rgb(0, 30, 98);
    color: rgb(0, 30, 98);
}

.chooseButton:hover {
    background-color: #ccc;
}

.dropDownHeader {
    margin: 20px;
}

.dropDownIcon {
    margin-right: 20px;
}

.hideDropDownInput {
    margin-top: 20px;
    background-color: rgb(0, 30, 98);
    color: #fff;
    border-radius: 5px;
    padding: 5px 16px;
    min-height: 48px;
    font-size: 14px;
    outline: none;
}

.label {
    color: #fff; 
    display: block;
    align-self: center;
    flex: 1 1 auto;
    margin-left: 22px;
    margin-right: 16px;
    color: #fff;
    font-size: 32px;
    font-weight: 700;
}

.search {
    display: flex;
    align-items: stretch;
    overflow: hidden;
    height: 40px;
    border: 1px solid #e8e8e8;
    border-radius: 4px;
    background-color: #fff;
    margin-bottom: 10px;
    width: 360px;
}

.searchIcon {
    width: 24px;
    min-height: 24px;
    margin-left: 8px;
    margin-right: 16px;
    align-self: center;
}

.searchInput {
    height: 100%;
    width: 0;
    flex: 1 0 auto;
    padding: 0;
    color: #4d4d4d;
    font-size: 16px;
    line-height: 24px;
    border: none;
    outline: none;
}

.radioInput {
    color: #4d4d4d;
    font-size: 16px;
    line-height: 24px;
    height: 48px;
    
}

.hideMap {
    display: flex;
    align-items: center;
    margin-left: 16px;
}

.hideMap button {
    border-radius: 4px;
    border: 2px solid #fff;
    padding: 5px 16px 5px 16px;
    background-color: transparent;
    color: #fff;
    fill: #fff;
    min-height: 48px;
    font-size: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
    line-height: 24px;
    text-align: center;
    margin: 0;
    cursor: pointer;
    outline: none;
    transition: background-color .2s linear,
                border-color .2s linear;
}

.hideMapIcon {
    margin-right: 10px;
}